/* Text styles */
.src-comp-ripple-rippleWrapper {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}
.src-comp-ripple-rippleWrapper.src-comp-ripple-hasRipples {
  z-index: 1;
}

.src-comp-ripple-ripple {
  position: absolute;
  transform: scale(0);
  border-radius: 100%;
  opacity: 0.25;
  animation-name: src-comp-ripple-ripple;
}

@keyframes src-comp-ripple-ripple {
  to {
    opacity: 0;
    transform: scale(2);
  }
}