/* Buttons */
/* Text styles */
.src-comp-button-button {
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  border-radius: 8px;
  padding: 0;
  height: 48px;
  line-height: 48px;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
  user-select: none;
  position: relative;
  flex-shrink: 0;
  text-align: center;
  cursor: pointer;
  transition: color 0.2s, background 0.2s;
  overflow: hidden;
  box-shadow: 0px 1px 1px 0px var(--border), 0px 2px 1px 0px var(--border), 0px 1px 3px 0px var(--border);
  justify-content: center;
}
.src-comp-button-button.src-comp-button-grey {
  background: var(--background-secondary);
  color: var(--content-1);
}
.src-comp-button-button.src-comp-button-grey .src-comp-button-ripple {
  background: var(--content-1);
}
.src-comp-button-button.src-comp-button-grey:hover {
  background: var(--border);
}
.src-comp-button-button.src-comp-button-grey.src-comp-button-active {
  background: var(--border);
}
.src-comp-button-button.src-comp-button-grey.src-comp-button-disabled {
  background: var(--border) !important;
  color: var(--content-3) !important;
  cursor: default;
  pointer-events: none;
}
.src-comp-button-button.src-comp-button-grey.src-comp-button-disabled.src-comp-button-bordered {
  border-left: 1px solid var(--background-primary);
}
.src-comp-button-button.src-comp-button-grey.src-comp-button-bordered {
  border-left: 1px solid var(--border);
}
.src-comp-button-button.src-comp-button-white {
  background: var(--background-primary);
  color: var(--content-1);
}
.src-comp-button-button.src-comp-button-white .src-comp-button-ripple {
  background: var(--content-1);
}
.src-comp-button-button.src-comp-button-white:hover {
  background: var(--border);
}
.src-comp-button-button.src-comp-button-white.src-comp-button-active {
  background: var(--border);
}
.src-comp-button-button.src-comp-button-white.src-comp-button-disabled {
  background: var(--border) !important;
  color: var(--content-3) !important;
  cursor: default;
  pointer-events: none;
}
.src-comp-button-button.src-comp-button-white.src-comp-button-disabled.src-comp-button-bordered {
  border-left: 1px solid var(--background-primary);
}
.src-comp-button-button.src-comp-button-white.src-comp-button-bordered {
  border-left: 1px solid var(--border);
}
.src-comp-button-button.src-comp-button-blue {
  background: var(--blue-base);
  color: var(--background-primary);
}
.src-comp-button-button.src-comp-button-blue .src-comp-button-ripple {
  background: var(--background-primary);
}
.src-comp-button-button.src-comp-button-blue:hover {
  background: var(--blue-hover);
}
.src-comp-button-button.src-comp-button-blue.src-comp-button-active {
  background: var(--blue-hover);
}
.src-comp-button-button.src-comp-button-blue.src-comp-button-disabled {
  background: var(--border) !important;
  color: var(--content-3) !important;
  cursor: default;
  pointer-events: none;
}
.src-comp-button-button.src-comp-button-blue.src-comp-button-disabled.src-comp-button-bordered {
  border-left: 1px solid var(--background-primary);
}
.src-comp-button-button.src-comp-button-blue.src-comp-button-bordered {
  border-left: 1px solid var(--blue-hover);
}
.src-comp-button-button.src-comp-button-hasIcon.src-comp-button-iconLeft.src-comp-button-small .src-comp-button-icon {
  margin-right: 4px;
}
.src-comp-button-button.src-comp-button-hasIcon.src-comp-button-iconLeft.src-comp-button-medium .src-comp-button-icon {
  margin-right: 8px;
}
.src-comp-button-button.src-comp-button-hasIcon.src-comp-button-iconRight.src-comp-button-small .src-comp-button-icon {
  margin-left: 4px;
}
.src-comp-button-button.src-comp-button-hasIcon.src-comp-button-iconRight.src-comp-button-medium .src-comp-button-icon {
  margin-left: 8px;
}
.src-comp-button-button.src-comp-button-hasIcon.src-comp-button-iconBoth.src-comp-button-small .src-comp-button-icon {
  margin-right: 4px;
}
.src-comp-button-button.src-comp-button-hasIcon.src-comp-button-iconBoth.src-comp-button-small .src-comp-button-icon ~ .src-comp-button-icon {
  margin-right: 0;
  margin-left: 4px;
}
.src-comp-button-button.src-comp-button-hasIcon.src-comp-button-iconBoth.src-comp-button-medium .src-comp-button-icon {
  margin-right: 8px;
}
.src-comp-button-button.src-comp-button-hasIcon.src-comp-button-iconBoth.src-comp-button-medium .src-comp-button-icon ~ .src-comp-button-icon {
  margin-right: 0;
  margin-left: 8px;
}
.src-comp-button-button.src-comp-button-hasIcon.src-comp-button-iconOnly {
  border-radius: 50%;
  padding: 0 8px;
}
.src-comp-button-button.src-comp-button-hasIcon.src-comp-button-iconOnly.src-comp-button-isLoading .src-comp-button-icon {
  display: none;
}
.src-comp-button-button.src-comp-button-isSubmit input {
  width: 100%;
  height: 100%;
  border: 0;
  padding: 0;
  display: block;
  background: none;
  color: inherit;
  cursor: inherit;
  font-size: inherit;
  text-align: inherit;
  line-height: inherit;
  font-weight: inherit;
  text-transform: inherit;
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  border-radius: 32px;
}
.src-comp-button-button.src-comp-button-small {
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  height: 32px;
  line-height: 32px;
  padding: 0 12px;
}
.src-comp-button-button.src-comp-button-small .src-comp-button-icon {
  font-size: 16px;
}
.src-comp-button-button.src-comp-button-small .src-comp-button-loader {
  width: 16px;
  height: 16px;
}
.src-comp-button-button.src-comp-button-medium {
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  padding: 0 16px;
  height: 40px;
  line-height: 40px;
}
.src-comp-button-button.src-comp-button-medium .src-comp-button-icon {
  font-size: 24px;
}
.src-comp-button-button.src-comp-button-medium .src-comp-button-loader {
  width: 24px;
  height: 24px;
}
.mobile .src-comp-button-button.src-comp-button-medium.src-comp-button-smallInMobile {
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  height: 32px;
  line-height: 32px;
  padding: 0 12px;
}
.mobile .src-comp-button-button.src-comp-button-medium.src-comp-button-smallInMobile .src-comp-button-icon {
  font-size: 16px;
}
.mobile .src-comp-button-button.src-comp-button-medium.src-comp-button-smallInMobile .src-comp-button-loader {
  width: 16px;
  height: 16px;
}
.src-comp-button-button.src-comp-button-noShadow {
  box-shadow: none !important;
}
.src-comp-button-button .src-comp-button-icon {
  vertical-align: middle;
}
.src-comp-button-button > span {
  display: inline-block;
  vertical-align: middle;
}
.src-comp-button-button .src-comp-button-loader {
  display: inline-block;
  vertical-align: middle;
  border-radius: 32px;
  transform-origin: center;
  border: 2px solid;
  border-color: currentColor;
  border-right-color: transparent;
}
.src-comp-button-button .src-comp-button-loader {
  animation: rotating 1s linear infinite, fadeIn 0.5s linear;
}

.src-comp-button-hidden {
  position: absolute;
  right: -999999999px;
}